@media (min-width : 1050px) {
    .name-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        padding-top: 15%;
    }
    .greeting-box {
        font-size: 59px;
    }
    .myname-box {
        font-size: 110px;
    }
    .undergrad-box {
        font-size: 86px;
    }
}
@media (max-width: 600px) {
    .name-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        top:100px;
        padding-top: 27%;
        line-height: 1.6;
    }
    .greeting-box {
        font-size: 33px;
        line-height: 1;
    }
    .myname-box {
        font-size: 47px;
    }
    .undergrad-box {
        font-size: 35px;
    }
}
@media (max-width: 376px) {
    .name-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        padding-top: 30%;
        line-height: 1.2;
    }
    .gap-div {
        padding-top: 20px;
    }
    .greeting-box {
        font-size: 40px;
        line-height: 1;
    }
    .myname-box {
        font-size: 40px;
    }
    .undergrad-box {
        font-size: 31px;
    }
}
@media (max-width: 360px) {
    .name-container-box {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 45px;
        padding-top: 17%;
        line-height: 1.2;
    }
    .gap-div {
        padding-top: 20px;
    }
    .greeting-box {
        font-size: 35px;
        line-height: 1;
    }
    .myname-box {
        font-size: 35px;
    }
    .undergrad-box {
        font-size: 25px;
    }
}


